// @require '../../style/reset.scss'

import React from 'react';
import ReactDom from 'react-dom';
import Datepicker from '../index';

const Demo = function () {
  return (
    <div>
      <h2>Set Calendar Index</h2>
      <p>check the zIndex of the calendar Item. It's z-index has been set to 123.</p>
      <Datepicker
        calendarZindex={123}
        placeholder={'点击选择日期'}
      />
    </div>
  );
};

ReactDom.render(
  <Demo />,
  document.getElementById('demo12')
);
